<template>
  <md-card>
    <md-card-header>
      <div class="md-title">{{ title }}</div>
    </md-card-header>

    <md-card-content>
      {{ text }}
    </md-card-content>
    <md-card-actions>
      <md-button v-on:click="nextQuestion(3)" class="md-icon-button">
        <md-icon>favorite</md-icon>
      </md-button>
      <md-button v-on:click="nextQuestion(2)" class="md-icon-button">
        <md-icon>thumb_up</md-icon>
      </md-button>
      <md-button v-on:click="nextQuestion(1)" class="md-icon-button">
        <md-icon>thumb_down</md-icon>
      </md-button>
      <md-button v-on:click="nextQuestion(0)" class="md-icon-button">
        <md-icon><EmoticonAngry /></md-icon>
      </md-button>
    </md-card-actions>
  </md-card>
</template>

<script>
import EmoticonAngry from "vue-material-design-icons/EmoticonAngry.vue";

export default {
  name: "Card",
  components: { EmoticonAngry },
  data() {
    return {
      text: this.$parent.$parent.questions[this.$parent.currentQuestion]
        .question,
      title:
        "Question " +
        (this.$parent.currentQuestion + 1) +
        " of " +
        this.$parent.$parent.questions.length,
    };
  },
  methods: {
    nextQuestion(index) {
      if (
        this.$parent.currentQuestion !==
        this.$parent.$parent.questions.length - 1
      ) {
        this.$parent.transition = "slide";
        this.$parent.view = !this.$parent.view;
        this.$parent.currentQuestion += 1;
        this.$parent.$parent.answeredQuestions.push(index);
      } else {
        this.$parent.$parent.view = "Results";
        this.$parent.$parent.encodeAll();
      }
    },
  },
};
</script>

<style scoped>
.md-card {
  width: 320px;
  margin: 4px;
  display: inline-block;
  vertical-align: top;
}
</style>