<template>
  <div id="app">
    <transition name="fade" mode="out-in">
      <component v-bind:is="view" />
    </transition>
  </div>
</template>

<script>
import Index from "./components/Index.vue";
import Test from "./components/Test.vue";
import Results from "./components/Results.vue";
import questions from "./assets/questions.json";

function chunkArrayInGroups(arr, size) {
  var myArray = [];
  var itermax = Math.ceil(arr.length / size);
  for (var i = 0; i < itermax; i += 1) {
    myArray.push(arr.slice(i * size, i * size + size));
  }
  return myArray;
}

async function encodeAll() {
  var result = Buffer.from(
    chunkArrayInGroups(
      [].concat.apply(
        [],
        this.answeredQuestions.map((item) => {
          switch (item) {
            case 0:
              return ["0", "0"];
            case 1:
              return ["1", "0"];
            case 2:
              return ["0", "1"];
            case 3:
              return ["1", "1"];
          }
        })
      ),
      8
    ).map((item) => {
      return parseInt(item.join(""), 2);
    })
  );

  var hash = await crypto.subtle.digest("SHA-512", result);

  history.pushState(
    {},
    "",
    "?" + btoa(result.toString("base64") + Buffer.from(hash).toString("base64"))
  );
}

export async function decodeAll() {
  var result = atob(window.location.search.split("?")[1]);
  var hash = result.substring(result.length - 88);
  result = Buffer.from(result.substring(0, result.length - 88), "base64");
  if (
    Buffer.from(await crypto.subtle.digest("SHA-512", result)).toString(
      "base64"
    ) !== hash
  ) {
    return false;
  }

  var output = [];

  result.forEach((element) => {
    let out = element.toString(2);
    output.push(out);
  });

  result = chunkArrayInGroups(output.join(""), 2).map((item) => {
    switch (item.padEnd(2, "0")) {
      case "00":
        return 0;
      case "10":
        return 1;
      case "01":
        return 2;
      case "11":
        return 3;
    }
  });

  return result;
}

export default {
  name: "App",
  components: {
    Index,
    Test,
    Results,
  },
  data() {
    return {
      questions: questions,
      view: "Index",
      answeredQuestions: [],
    };
  },
  async mounted() {
    // yeah, this is an expensive ass function but i don't care
    if (decodeAll()) {
      this.answeredQuestions = await decodeAll();
      this.view = "Results";
    }
  },
  methods: {
    encodeAll: encodeAll,
  },
};
</script>

<style>
:root {
  --md-theme-default-primary: #ff91af !important;
  --md-theme-default-primary-on-background: #ff91af !important;
}
#app {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}
.fade-enter-active {
  animation: fade 0.5s reverse;
}
.fade-leave-active {
  animation: fade 0.5s;
}
@keyframes fade {
  0% {
    opacity: 100%;
  }
  100% {
    opacity: 0%;
  }
}
</style>
