        <card v-else key="card2" />
      </transition>
    </div>

    <div class="md-layout md-alignment-top-center">
      <md-button
        v-on:click="
          if (currentQuestion > 0) {
            transition = 'slider';
            view = !view;
            currentQuestion -= 1;
            $parent.answeredQuestions.splice(-1);
          } else {
            $parent.view = 'Index';
          }
        "
        >back</md-button
      >
    </div>
