<script>
// try not to over think this
import { Radar, mixins } from "vue-chartjs";
const { reactiveProp } = mixins;

export default {
  extends: Radar,
  mixins: [reactiveProp],
  data() {
    return {
      options: {
        title: {
          display: true,
          text: this.ideology,
          fontFamily:
            "Roboto,Noto Sans,-apple-system,BlinkMacSystemFont,sans-serif;",
        },
        startAngle: 90,
        elements: {
          line: {
            borderWidth: 0,
          },
          point: {
            radius: 0,
          },
        },
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          display: false,
        },
        tooltips: { enabled: false },
        hover: { mode: null },
        scale: {
          ticks: {
            display: false,
            min: 0,
            max: 100,
          },
          gridLines: {
            color: "rgba(255,255,255,0.12)",
          },
          angleLines: {
            color: "rgba(255,255,255,0.12)",
          },
          pointLabels: {
            fontFamily:
              "Roboto,Noto Sans,-apple-system,BlinkMacSystemFont,sans-serif;",
          },
        },
      },
    };
  },
  props: {
    chartData: [],
    watching: Boolean,
    ideology: String,
  },
  mounted() {
    this.renderChart(this.chartData, this.options);
  },
  methods: {
    exportImg() {
      let canvas = document
        .getElementById("radar-chart")
        .toDataURL("image/png");
      let link = document.createElement("a");
      link.download = "image";
      link.href = canvas;
      link.click();
    },
  },
  watch: {
    chartData: function () {
      this.renderChart(this.chartData, this.options);
    },
  },
};
</script>