<template>
  <div>
    <p class="md-title md-layout md-alignment-top-center">VizValues</p>

    <radar-chart
      :height="320"
      :ideology="finalIdeology"
      :chartData="calcChart"
      ref="thisDumbassChart"
    />

    <div class="md-layout md-alignment-top-center">
      <md-button
        v-on:click="$refs.thisDumbassChart.exportImg()"
        class="md-icon-button"
      >
        <md-icon>download</md-icon>
      </md-button>
    </div>

    <div class="md-layout md-alignment-top-center">
      <md-button v-on:click="$parent.view = 'Index'">back</md-button>
    </div>
  </div>
</template>

<script>
import RadarChart from "./radarchart.vue";
import ideologies from "../assets/ideologies.json";

export default {
  name: "Results",
  components: {
    RadarChart,
  },
  mounted() {},
  computed: {
    finalIdeology: function () {
      const effects = ["auth", "cap", "con"];

      var maxvalues = {
        max: {
          auth: 0,
          cap: 0,
          con: 0,
        },
        min: {
          auth: 0,
          cap: 0,
          con: 0,
        },
      };

      var values = {
        auth: 0,
        cap: 0,
        con: 0,
      };

      this.$parent.questions.forEach((element) => {
        for (const effect of effects) {
          if (element.effect[effect] >= 0) {
            maxvalues.max[effect] += element.effect[effect];
            maxvalues.min[effect] += element.effect[effect] * -1;
          } else {
            maxvalues.max[effect] += element.effect[effect] * -1;
            maxvalues.min[effect] += element.effect[effect];
          }
        }
      });

      var answers = this.$parent.answeredQuestions.map((item) => {
        switch (item) {
          case 0:
            return -1;
          case 1:
            return -0.5;
          case 2:
            return 0.5;
          case 3:
            return 1;
        }
      });

      for (var i = 0; i < this.$parent.answeredQuestions.length; i++) {
        for (const effect of effects) {
          values[effect] +=
            this.$parent.questions[i].effect[effect] * answers[i];
        }
      }

      for (const key in values) {
        values[key] =
          (values[key] - maxvalues.min[key]) /
          ((maxvalues.max[key] - maxvalues.min[key]) * 0.01);
      }

      return ideologies
        .map((item) => {
          return [
            Math.abs(100 - item.stats.econ - values.cap) +
              Math.abs(100 - item.stats.govt - values.auth) +
              Math.abs(100 - item.stats.scty - values.con),
            item.name,
          ];
        })
        .sort(function (a, b) {
          return a[0] - b[0];
        })[0][1];
    },
    calcChart: function () {
      const effects = ["auth", "cap", "con"];

      var maxvalues = {
        max: {
          auth: 0,
          cap: 0,
          con: 0,
        },
        min: {
          auth: 0,
          cap: 0,
          con: 0,
        },
      };

      var values = {
        auth: 0,
        cap: 0,
        con: 0,
      };

      this.$parent.questions.forEach((element) => {
        for (const effect of effects) {
          if (element.effect[effect] >= 0) {
            maxvalues.max[effect] += element.effect[effect];
            maxvalues.min[effect] += element.effect[effect] * -1;
          } else {
            maxvalues.max[effect] += element.effect[effect] * -1;
            maxvalues.min[effect] += element.effect[effect];
          }
        }
      });

      var answers = this.$parent.answeredQuestions.map((item) => {
        switch (item) {
          case 0:
            return -1;
          case 1:
            return -0.5;
          case 2:
            return 0.5;
          case 3:
            return 1;
        }
      });

      for (var i = 0; i < this.$parent.answeredQuestions.length; i++) {
        for (const effect of effects) {
          values[effect] +=
            this.$parent.questions[i].effect[effect] * answers[i];
        }
      }

      for (const key in values) {
        values[key] =
          (values[key] - maxvalues.min[key]) /
          ((maxvalues.max[key] - maxvalues.min[key]) * 0.01);
      }

      return {
        labels: [
          "Authoritarian",
          "Capitalist",
          "Conservative",
          "Libertarian",
          "Socialist",
          "Progressive",
        ],
        datasets: [
          {
            label: "Radar Chart",
            backgroundColor: "#ff91af",
            data: [
              ...Object.values(values),
              ...Object.values(values).map((item) => {
                return 100 - item;
              }),
            ],
          },
        ],
      };
    },
  },
};
</script>

<style scoped>
.md-icon-button {
  margin-top: 20px !important;
}
</style>